}

.dark .bg-gray-100,
[data-theme="dark"] .bg-gray-100 {
  background-color: var(--bg-body) !important;
}

/* Text overrides */
.dark .text-gray-900,
[data-theme="dark"] .text-gray-900 {
  color: var(--text-primary) !important;
}

.dark .text-gray-800,
[data-theme="dark"] .text-gray-800 {
